var React = require('react');
var Reflux = require('reflux');
var FilterItemList = require('./filterItemList.jsx');
var KeywordSearch = require('./keywordSearch.jsx');
var AllNoneSelector = require('./allNoneSelector.jsx');
var SelectionCounter = require('./selectionCounter.jsx');

var showOnlySelected = false;
var FilterGroup = React.createClass({displayName: "FilterGroup",

    _onCounterClicked: function(selected) {
        this.showOnlySelected = selected;
        this.forceUpdate();
    },

    _filterByKeyword: function (keyword) {
        var items;
        if (keyword) {
            // filter the collection
            var self = this;
            var pattern = new RegExp(keyword, 'i');
            var noResults = true;
            this.props.allItems.map(function (item) {
                if (!pattern.test(item.name)){
                    item.hide = true;
                } else {
                    item.hide = false;
                    noResults = false;
                }
            });
            self.setState({"noResults": noResults});             
        } else {
            // display the original collection
            this.props.allItems.map(function (item) {
                item.hide = false;
            });
            self.setState({"noResults": false});
        }
        this.forceUpdate();
    },

    componentDidMount: function(){
        $(this.getDOMNode()).find('.filter-list-container').mCustomScrollbar({theme:"inset-dark"});
    },

    getInitialState: function() {
        this.state = this.state || {"noResults": false};
        return this.state;
    },

    render: function() {
        //console.log("filters->filter-group: render - " + this.props.filterDefinition.label);
        var filterDefinition = this.props.filterDefinition;
        var items = this.props.allItems || [];
        var itemsSelected = this.props.allItems.filter(function (data) {return (data.selected);});
        var self = this;
        var noResults = "";
        if (this.state.noResults){
            var noResults = 
                React.createElement("div", {className: "filter-no-results"}, 
                    React.createElement("br", null), React.createElement(Message, {message: "filters.noResults"})
                );
        }
        return(
            React.createElement("div", {className: "filter-group-panel selected"}, 
                React.createElement("div", {className: "filter-group-panel-header"}, 
                    React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: this.props.filterDefinition.label})), 
                    React.createElement(SelectionCounter, {selected: itemsSelected.length, total: items.length, onCounterClicked: this._onCounterClicked}), 
                    React.createElement(AllNoneSelector, {filterType: filterDefinition.param, onAllNoneClicked: self.props.onAllNoneClicked})
                ), 
                React.createElement(KeywordSearch, {onSearch: this._filterByKeyword}), 
                noResults, 
                React.createElement("div", {className: "filter-list-container"}, 
                    React.createElement(FilterItemList, {
                        onItemChanged: this.props.onItemChanged, 
                        items: items, 
                        filterDefinition: this.props.filterDefinition, 
                        showOnlySelected: this.showOnlySelected})
                )
            )
            );

    }
});

module.exports = FilterGroup;
