'use strict';

var React = require('react');
var Reflux = require('reflux');
var FilterStore=require('../../../stores/filterStore.js')
var FilterActions=require('../../../actions/filterActions.js')

var DateFilter = React.createClass({displayName: "DateFilter",

    _clearStartDate: function(ev){
        $('.start-date').datepicker('setDate', null);
        this.props.onValueChanged('sd', '');  
    },    

    _clearEndDate: function(ev){
        $('.end-date').datepicker('setDate', null);
        this.props.onValueChanged('ed', '');   
    },

    componentDidUpdate: function(){
        console.log("control-pane->filters->dateFilter-->componentDidUpdate");
        if(this.props && this.props.resetDates){
            FilterActions.resetDates();
            if($('.start-date').val()!=""){
                this._clearStartDate();
            }
            if($('.end-date').val()!=""){
                this._clearEndDate();
            }
        }
    },

 
    componentDidMount: function(){
        var self = this;
        $('.start-date').datepicker({
                dateFormat: "dd/mm/yy",
                changeMonth: true,
                changeYear: true,
                onSelect: function(dateText) {
                    self.props.onValueChanged('sd', dateText);
                }
            });
        $('.end-date').datepicker({
                dateFormat: "dd/mm/yy",
                changeMonth: true,
                changeYear: true,
                onSelect: function(dateText) {
                    self.props.onValueChanged('ed', dateText);
                }
            });      
    },    

    render: function() {  
              
        return(
            React.createElement("div", {className: "filter-group-panel selected"}, 
                React.createElement("div", {className: "filter-group-panel-header"}, 
                    React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.date"}))
                ), 
                React.createElement("div", {className: "input-group date"}, 
                    React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.startDate"})), 
                    React.createElement("input", {type: "text", className: "start-date", onChange: this._changeStartDate}), 
                    React.createElement("i", {className: "fa fa-trash", onClick: this._clearStartDate})
                ), 
                React.createElement("div", {className: "input-group date"}, 
                    React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.endDate"})), 
                    React.createElement("input", {type: "text", className: "end-date", onChange: this._changeEndDate}), 
                    React.createElement("i", {className: "fa fa-trash", onClick: this._clearEndDate})
                )
            )
            );  
        
    }
});

module.exports = DateFilter;